'use strict';

require('../../styles/templates/product.frame.scss');

var _productFinish = require('../components/product-finish');

var _customFrameSize = require('../components/customFrameSize');

// Components
document.addEventListener('DOMContentLoaded', function () {
  (0, _productFinish.init)();
  (0, _customFrameSize.init)();
});

//Reinit when theme section settings have been updated
// Import CSS
$(document).on('shopify:section:load', function () {
  (0, _productFinish.init)();
  (0, _customFrameSize.init)();
});